@import url(https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap);
 body {
  margin: 0;
  font-family: 'Noto Serif', serif;
  color: #3c3836;
  font-size: 10pt;
}

a:link, a:visited {
  text-decoration: none;
  color: #7c6f64;
  transition: 0.4s;
}

a:hover {
  color: #cb4b16;
}

.wrapper {
  height: 750px;
  width: 1200px;
  background-color: #bdae93;
}
.ibox {
  width: 700px;
  height: 500px;
}
.ibox-inner {
  width: 100%;
  height: 100%;
  background-color: black;
}
.ibox-inner > svg {
  width: 100%;
  height: 100%;
}
.tbox {
  width: 500px;
  height: 750px;
  background: #ebdbb2;
  overflow: hidden;
  padding: 0px;
  position: absolute;
  left: 700px;
  top: 0;
}
.tbox-inner {
  padding: 4px;
}
.mdata {
  padding-left: 15px;
  padding-right: 15px;
  padding-top: 15px;
}
.recs {
  padding-top: 8px;
  padding-left: 40px;
  padding-right: 15px;
}
.recs > span {
  line-height: 1.3em;
}
.thumbcon {
  height: 75px;
  width: 75px;
  text-align:center;
  border: 4px solid #ebdbb2;
}
.thumb {
  height: 75px;
  width: 75px;
  object-fit: cover;
  cursor: pointer;
}
.flex-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.pano-bars-outer {
  padding: 4px;
  margin-top: 12px;
  border: 1pt solid;
  border-radius: 4px;
  height: 140px;
  width: 450px;
}

.pano-bars-inner {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 118px;
  width: 475px;
  padding-left: 12px;
}

.pano-bars-title {
  padding-bottom: 8px;
}

.pano-bar-size {
  box-sizing: border-box;
  width: 200px;
  height: 1.3em;
  line-height: 1em;
  padding: 0px;
}

.pano-bar-inner {
  float: left;
  position: relative;
  box-sizing:border-box;
  height: 100%;
  background-color: #7A9D96;
  padding: 0px;
  margin: 0px;
}

.pano-bar-text {
  font-size: 0.7em;
  padding-left: 0.5em;
}

.mbox {
  width: 700px;
  height: 250px;
  position: absolute;
  left: 0;
  top: 500px;
  background-color: #bdae93;
}
.mboxdata {
  padding: 25px;
}
.mboxtitle {
  font-size: 1.3em;
  padding-top: 0.5em;
  padding-bottom: 1em;
}
.mboxabout {
  font-size: 1.1em;
  padding-bottom: 1em;
}

hr {
  border-top: 1px solid black !important;
}

.archinfo {
  padding-top: 8px;
  padding-left: 16px;
  padding-right: 16px;
}

.inputcont-outer {
  margin-top: 14px;
}

.inputcont {
  position: relative;
  left: 100px;
}

/***************************************************************************/
/******  *********/

.anno-button-box {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 8px;
  text-align: center;
}

.anno-button {
  cursor: pointer;
  font-family: Special Elite;
  line-height: 1.1em;
  font-size: 1.2em;
  border-radius: 5px;
  border-style: solid;
  border-width: 2px;
  padding: 0;
  margin: 3px;
  margin-right: 6px;
  margin-left: 6px;
  height: 36px;
  width: 80px;
  text-align: center;
  background-color: #689d6a;
  border-color: #3b3b3b;
  color: #3b3b3b;
  opacity: 0.8;
}

.anno-button:hover {
  opacity: 1;
}

.anno-button:active {
  transform: scale(0.98);
}

.anno-button-prop:hover {
  transform: scale(1.01);
}

.anno-button-active {
  opacity: 1;
  background-color: #d79921;
}

/***************************************************************************/
/******  *********/

.tooltip {
  cursor: help;
  position: relative;
  display: inline-block;
  line-height: 1em;
}

.tooltip .tooltiptext {
  font-size: 0.8em;
  visibility: hidden;
  width: 250px;
  background-color: #928374;
  color: #fbf1c7;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 40%;
  margin-left: -125px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.tooltipright {
  cursor: help;
  position: relative;
  display: inline-block;
}

.tooltipright .tooltiptextright {
  font-size: 0.8em;
  visibility: hidden;
  width: 250px;
  background-color: #928374;
  color: #fbf1c7;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -200px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltipright .tooltiptextright::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 80%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltipright:hover .tooltiptextright {
  visibility: visible;
  opacity: 1;
}

.tooltipbottom {
  cursor: help;
  position: relative;
  display: inline-block;
}

.tooltipbottom .tooltiptextbottom {
  font-size: 0.8em;
  visibility: hidden;
  width: 250px;
  background-color: #928374;
  color: #fbf1c7;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  top: 150%;
  left: 50%;
  margin-left: -100px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltipbottom .tooltiptextbottom::before {
  content: "";
  position: absolute;
  top: -5px;
  left: 38%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent transparent #555 transparent;
}

.tooltipbottom:hover .tooltiptextbottom {
  visibility: visible;
  opacity: 1;
}

/****** reset ***************/
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
    margin:0;
    padding:0;
    border:0;
    outline:0;
    vertical-align:baseline;
    background:transparent;
}

html, body {
  padding: 0;
  height: 100%;
}

body {
    line-height:1;
}

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section {
    display:block;
}

nav ul {
    list-style:none;
}

blockquote, q {
    quotes:none;
}

blockquote:before, blockquote:after,
q:before, q:after {
    content:'';
    content:none;
}

a {
    margin:0;
    padding:0;
    vertical-align:baseline;
    background:transparent;
}

ins {
    background-color:#ff9;
    color:#000;
    text-decoration:none;
}

mark {
    background-color:#ff9;
    color:#000;
    font-style:italic;
    font-weight:bold;
}

del {
    text-decoration: line-through;
}

abbr[title], dfn[title] {
    border-bottom:1px dotted;
    cursor:help;
}

table {
    border-collapse:collapse;
    border-spacing:0;
}

/* change border colour to suit your needs */
hr {
    display:block;
    height:1px;
    border:0;
    border-top:1px solid #cccccc;
    margin:1em 0;
    padding:0;
}

input, select {
    vertical-align:middle;
}

::selection {
  background: var(--color-background); /* WebKit/Blink Browsers */
}

